import React, { useState, useEffect } from "react";

import TableComponent from "components/shared-components/TableComponent/Index";
import { Button, PageHeader, Progress, Row, Table, Tooltip } from "antd";
import { Col } from "antd";
import { SCREEN_ITEMS_TYPES, uploadProjectLegendColor } from "../../../constants/CommonConstants";
import { PlusCircleOutlined, InfoCircleOutlined, CheckCircleOutlined } from "@ant-design/icons";
import UploadData from "../../../components/UploadData/index";
import { camelCase } from "lodash";
import Legend from "components/shared-components/Legends/LegendMarks";
import sample_file from "assets/data/sample_file.xlsx";

const BAR_COLORS = ["#108ee9", "#87d068"];
const UploadProject = () => {
  const [showUploadModal, setShowUploadModal] = useState(false);
  const [tableData, setTableData] = useState([]);
  const [header, setHeader] = useState(null);
  const [progress, setProgress] = useState(0);
  const handleUploadCancel = () => {
    setShowUploadModal(false);
  };
  const Upload = () => {
    setShowUploadModal(true);
    setTableData([]);
    setProgress(0);
    setHeader(null);
  };
  const increaseProgress = (lines, totalRecords) => {
    const progress = Math.floor((lines / totalRecords) * 100);
    setProgress(progress);
  };
  const columnsNew =
    header &&
    header.map((col, index) => {
      return {
        title: col,
        key: col,
        dataIndex: camelCase(col),
        render: (text, record) => {
          return record.body.records[camelCase(col)];
        },
      };
    });
  const columnsWithInfo = columnsNew && [
    ...columnsNew,
    {
      title: "Info",
      dataIndex: "Info",
      key: "Info",
      render: (text, record) => (
        <div>
          <Tooltip title={record?.body?.message}>
            {record.statusCodeValue === 400 ? (
              <InfoCircleOutlined style={{ color: "red", fontSize: 25 }} />
            ) : (
              <CheckCircleOutlined style={{ color: "green", fontSize: 25 }} />
            )}
          </Tooltip>
        </div>
      ),
    },
  ];

  const getProjectResponse = body => {
    try {
      const reader = body.getReader();
      const decoder = new TextDecoder("utf-8");
      let buffer = "";
      let rows = [];
      let totalLines = 0;
      let totalRecords = 0;
      let rowsTobeAdded = [];
      const processResult = result => {
        buffer += decoder.decode(result.value || new Uint8Array(), { stream: !result.done });
        const lines = buffer.split("\n");
        buffer = lines.pop();
        totalLines += lines.length;
        for (const line of lines) {
          const data = JSON.parse(line);
          rows.push(data);
          if (rows.length >= 1) {
            setHeader(rows[0].body.records.columns);
            totalRecords = rows[0].body.records.recordsCount;
            rowsTobeAdded.push(data);
            setTableData(rowsTobeAdded);
          }
        }
        if (result.done) {
          return;
        }
        increaseProgress(totalLines, totalRecords);
        return reader.read().then(processResult);
      };
      return reader.read().then(processResult);
    } catch (error) {
      console.error("Something went wrong while uploading document", error);
    }
  };

  return (
    <>
      <PageHeader className="p-2 pl-2" title={" Update Billed Resources's Project Information"} />
      <Col className="mb-2 text-xl-right text-md-right " xs={24} md={12} lg={24} xl={24}>
        <Button type="primary" onClick={Upload}>
          <PlusCircleOutlined className="mr-2" />
          {"Upload file"}
        </Button>
      </Col>
      <Row className="mb-4">
        <Progress
          className="mt-5 mb-2"
          strokeColor={{
            "0%": BAR_COLORS[0],
            "100%": BAR_COLORS[1],
          }}
          percent={progress}
        />
      </Row>

      <TableComponent columns={columnsWithInfo} data={tableData.slice(1)} rowKey={record => record.key} />

      {showUploadModal && (
        <UploadData
          visible={showUploadModal}
          onCancel={handleUploadCancel}
          screen={SCREEN_ITEMS_TYPES.uploadProject}
          getProjectResponse={getProjectResponse}
          sampleFile={sample_file}
        />
      )}
    </>
  );
};

export default UploadProject;
